.container {
  min-width: 100vw;
  min-height: 75vh;
  height: auto;
  width: auto;
}

.grid_container {
  padding: 15px;
  display: grid;
  grid-template-columns: 67.6% auto;
  grid-template-rows: 50% 50%;
  -moz-column-gap: 8px;
  column-gap: 8px;
  row-gap: 8px;
  height: 80vh;
  width: 100%;
  object-fit: cover;
  position: relative;
}
.grid_item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.75s;
}

.grid_item:hover img {
  transform: scale(1.15);
}

.grid_item {
  transform: scale(1);
  transition: transform 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
}

.grid_item:nth-child(1) {
  grid-column: 1/2;
  grid-row: 1/-1;
  width: 100%;
  height: 100%;
}

.grid_item:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/2;
  width: 100%;
  height: 100%;
}

.grid_item:nth-child(3) {
  grid-column: 2/3;
  grid-row: 2/3;

  width: 100%;
  height: 100%;
}

@media (max-width: 1023px) {
  .container {
   min-height: 50vh;
  }
}
